<template>
  <div class="min-vh-100 bg-light py-5">
    <b-container class="bg-white rounded-4 shadow-lg p-4 mx-auto" style="max-width: 700px;">
      <h1 class="text-primary text-center mb-4" style="user-select:none;">
        ‚úçÔ∏è My Online Journal
      </h1>

      <!-- PASSWORD LOCK SCREEN -->
      <div v-if="!unlocked" class="text-center">
        <b-form-group
          label="Enter Password to Unlock"
          label-for="passwordInput"
          label-class="fw-semibold text-primary"
          class="mb-3"
          label-cols-sm="12"
          label-cols-md="6"
          label-align-sm="left"
        >
          <b-form-input
            id="passwordInput"
            type="password"
            v-model="passwordInput"
            placeholder="Enter password"
            class="text-primary"
            autocomplete="off"
          ></b-form-input>
        </b-form-group>
        <b-button
          variant="primary"
          :disabled="!passwordInput || !passwordInput.trim()"
          @click="checkPassword"
          class="mb-3"
        >
          Unlock
        </b-button>
        <b-alert
          variant="danger"
          show
          v-if="error"
          class="mt-2"
          dismissible
          @dismissed="error = false"
        >
          Incorrect password. Please try again.
        </b-alert>
      </div>

      <!-- JOURNAL ENTRIES SCREEN -->
      <div v-else>
        <!-- Journal Entry Form -->
        <b-form @submit.prevent="addEntry" class="mb-4">
          <b-form-group
            label="New Entry"
            label-for="newEntry"
            label-class="fw-semibold text-primary"
            label-cols-sm="12"
            label-cols-md="3"
            label-align-sm="left"
          >
            <b-form-textarea
              id="newEntry"
              v-model="newEntry"
              placeholder="What's on your mind today?"
              rows="5"
              class="text-primary"
              required
            ></b-form-textarea>
          </b-form-group>
          <div class="text-end">
            <b-button type="submit" variant="primary" class="fw-semibold">
              Add Entry
            </b-button>
          </div>
        </b-form>

        <!-- Journal Entries -->
        <div v-if="entries.length">
          <b-card
            v-for="(entry, index) in entries"
            :key="index"
            class="mb-3"
            border-variant="primary"
            bg-variant="light"
            text-variant="dark"
          >
            <small class="text-muted mb-2 d-block" style="font-family: monospace;">
              {{ formatDate(entry.date) }}
            </small>
            <p style="white-space: pre-line;">{{ entry.text }}</p>
          </b-card>
        </div>

        <div v-else class="text-center text-muted fw-semibold">
          No journal entries yet.
        </div>
      </div>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "JournalView",
  data() {
    return {
      newEntry: "",
      entries: [],
      passwordInput: "",
      unlocked: false,
      error: false,
    };
  },
  mounted() {
    const saved = localStorage.getItem("journalEntries");
    if (saved) {
      this.entries = JSON.parse(saved);
    }
  },
  methods: {
    addEntry() {
      if (!this.newEntry.trim()) return;
      const newLog = {
        text: this.newEntry.trim(),
        date: new Date(),
      };
      this.entries.unshift(newLog);
      this.newEntry = "";
      localStorage.setItem("journalEntries", JSON.stringify(this.entries));
    },
    formatDate(date) {
      return new Date(date).toLocaleString("en-US", {
        dateStyle: "medium",
        timeStyle: "short",
      });
    },
checkPassword() {
  if (!this.passwordInput.trim()) return;

  const payload = JSON.stringify({
    passwordInput: this.passwordInput.trim(),
  });

  console.log("Sending payload to Lambda:", payload);

  fetch("https://reevkfao7slsdwkianyo72vx540mtnyx.lambda-url.us-east-1.on.aws", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: payload,
  })
    .then(res => res.json())
    .then(data => {
      console.log("Lambda response:", data);
      if (data.message === "Access granted") {
        this.unlocked = true;
        this.error = false;
        this.passwordInput = "";
      } else {
        this.error = true;
      }
    })
    .catch(err => {
      console.error("Error during password check:", err);
      this.error = true;
    });
},
  },
};
</script>

<style scoped>
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>
